import fulfillMentStatusTag from "./../../components/ShopifyStatus";
import popItems from "./../../components/PopItems";
import XEUtils from "xe-utils";
import DialogNotice from './../../components/Recharge/DialogNotice';
import shopCate from './../../components/shopCate';
export default {
  props: ["fulfillArr", "dateArr"],
  data: function data() {
    return {
      options: [{
        id: 1,
        label: 'Shopify',
        children: []
      }, {
        id: 2,
        label: 'Woocommerce',
        children: []
      }, {
        id: 3,
        label: 'Lazada',
        children: []
      }, {
        id: 4,
        label: 'Shopee',
        children: []
      }],
      cascaderKey: 1,
      loading: false,
      tableHeight: 400,
      ce: this.$CE,
      //
      vendors: [],
      filterParams: {
        vendorId: "",
        vendorName: "",
        shopifyStore: [],
        timeFilter: "",
        dateFrom: "",
        dateTo: "",
        sortBy: "",
        orderMin: 0,
        orderMax: 0
      },
      filterParamsDefault: "{}",
      orderMin: undefined,
      orderMax: undefined,
      storeList: [],
      shopifyStoreName: "",
      filterDate: [],
      popDateFilter: false,
      popStoreFilter: false,
      popCodeFilter: false,
      allChecked: true,
      tableData: [],
      AllTableData: [],
      //
      submitI: 0,
      // dialogSetOrderPeriod: {
      //   isShow: false,
      //   loading: false,
      //   checked: false,
      //   period: undefined,
      // },
      // dialogSetOrderPeriodDefault: '{}',
      dialogToGenInvoice: {
        isShow: false,
        loading: false,
        paymentType: [1, 2, 3],
        onlyOne: []
      },
      dialogToGenInvoiceDefault: "{}",
      paymentArr: [{
        text: "By AliExpress VIP link",
        val: 2
      }, {
        text: "PayPal",
        val: 1
      }, {
        text: "Bank transfer",
        val: 3
      }],
      lastId: "",
      //id
      dialogNoticeData: {
        isShow: false
      }
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.filterParamsDefault = JSON.stringify(this.filterParams);

    if (this.$route.params.id) {
      this.vendors.push({
        vendorName: this.$route.params.name,
        id: this.$route.params.id
      });
      this.filterParams.vendorId = this.$route.params.id;
      this.filterParams.vendorName = this.$route.params.name;
    } // this.dialogSetOrderPeriodDefault = JSON.stringify(this.dialogSetOrderPeriod)


    this.dialogToGenInvoiceDefault = JSON.stringify(this.dialogToGenInvoice);
    this.getVendors();
    this.getStore();
    this.$nextTick(function () {
      // this.$emit("getTableHeight", this);
      _this.getItems();

      setTimeout(function () {
        $('.my-tree > .vxe-list--virtual-wrapper').css({
          'min-height': $(window).height() - 400 + 'px',
          'height': $(window).height() - 400 + 'px'
        });
      }, 1100);

      window.onresize = function () {
        $('.my-tree > .vxe-list--virtual-wrapper').css({
          'min-height': $(window).height() - 400 + 'px',
          'height': $(window).height() - 400 + 'px'
        }); // this.$emit("getTableHeight", this);
      };
    });
  },
  destroyed: function destroyed() {
    window.onresize = function () {};
  },
  components: {
    fulfillMentStatusTag: fulfillMentStatusTag,
    popItems: popItems,
    DialogNotice: DialogNotice,
    shopCate: shopCate
  },
  computed: {
    AllOrderChecked: function AllOrderChecked() {
      var orderNum = 0,
          checkedOrderNum = 0;
      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          vendor.children.forEach(function (store) {
            store.children.forEach(function (order) {
              if (order._VISIBLE) orderNum++;
              if (order.checked) checkedOrderNum++;
            });
          });
        }
      });
      return orderNum && orderNum === checkedOrderNum;
    },
    checkedVariableId: function checkedVariableId() {
      var ids = [];
      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          vendor.children.forEach(function (store) {
            store.children.forEach(function (order) {
              if (order.checked) {
                order.items.forEach(function (item) {
                  ids.push(item.variableId);
                });
              }
            });
          });
        }
      });
      return Array.from(new Set(ids));
    },
    totalVendorsNum: function totalVendorsNum() {
      // vendors
      var num = 0;
      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          var checked = false;
          vendor.children.forEach(function (store) {
            if (store.children.some(function (order) {
              return order.checked;
            })) {
              checked = true;
            }
          });
          if (checked) num++;
        }
      });
      return num;
    },
    totalOrdersNum: function totalOrdersNum() {
      //
      var arr = [];
      this.tableData.forEach(function (item) {
        if (item._LEVEL === 0) {
          item.children.forEach(function (store) {
            store.children.forEach(function (order) {
              if (order.checked) arr.push(order.id);
            });
          });
        }
      });
      return Array.from(new Set(arr)).length;
    },
    totalAllOrdersNum: function totalAllOrdersNum() {
      //
      var arr = [];
      this.tableData.forEach(function (item) {
        if (item._LEVEL === 0) {
          item.children.forEach(function (store) {
            store.children.forEach(function (order) {
              if (order._VISIBLE) arr.push(order.id);
            });
          });
        }
      });
      return Array.from(new Set(arr)).length;
    },
    purchaseNum: function purchaseNum() {
      var num = 0;
      this.tableData.forEach(function (item) {
        if (item._LEVEL === 0) {
          item.children.forEach(function (store) {
            if (store.children.some(function (order) {
              return order.checked;
            })) num++;
          });
        }
      });
      return num;
    }
  },
  watch: {
    AllOrderChecked: function AllOrderChecked() {
      this.allChecked = this.AllOrderChecked;
    },
    tableHeight: function tableHeight() {
      var _this2 = this;

      this.$nextTick(function () {
        _this2.refreshTree();
      });
    },
    "filterParams.shopifyStore": function filterParamsShopifyStore() {
      var _this3 = this;

      if (!this.filterParams.shopifyStore.length) return;
      var arr = [];
      this.filterParams.shopifyStore.forEach(function (e) {
        _this3.storeList.forEach(function (item) {
          if (e == item.shopifyAccountId) {
            arr.push(item.store);
          }
        });
      });
      this.shopifyStoreName = arr.join(",");
    }
  },
  methods: {
    changeAllChecked: function changeAllChecked() {
      var _this4 = this;

      //
      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          vendor.children.forEach(function (store) {
            _this4.$set(store, "checked", _this4.allChecked);

            store.children.forEach(function (order) {
              _this4.$set(order, "checked", _this4.allChecked);
            });
          });
        }
      });
    },
    unAllocate: function unAllocate() {
      var _this5 = this;

      //
      this.$confirm("Un-allocate selected products? (orders: ".concat(this.totalOrdersNum, ", products: ").concat(this.checkedVariableId.length, ")"), "", {
        confirmButtonText: "Confirm",
        cancelButtonText: "Discard",
        confirmButtonClass: "el-button--danger",
        type: "info"
      }).then(function () {
        _this5.loading = true;

        _this5.$apiCall("api.DispatchOrderRule.unAllocate", {
          variableIds: _this5.checkedVariableId
        }, function (r) {
          if (r.ErrorCode == "9999") {
            _this5.$emit("getNum");

            _this5.getItems(true);

            _this5.$elementMessage("UN-allocate Success", "success");
          } else {
            _this5.$elementMessage(r.Message, "error");
          }
        });
      });
      setTimeout(function () {
        document.querySelectorAll(".el-button--primary").forEach(function (el) {
          el.blur();
        });
      }, 200);
    },
    openToGenInvoice: function openToGenInvoice() {
      var _this6 = this;

      this.dialogToGenInvoice = JSON.parse(this.dialogToGenInvoiceDefault);
      this.dialogToGenInvoice.paymentType = localStorage.getItem("c_defaultPaymentType") ? JSON.parse(localStorage.getItem("c_defaultPaymentType")) : [];
      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          vendor.children.forEach(function (store) {
            store.children.forEach(function (order) {
              if (order.checked) {
                order.items.forEach(function (item) {
                  if (item.onlyOne == 1) {
                    item.vendorName = vendor.vendorName;

                    _this6.dialogToGenInvoice.onlyOne.push(item);
                  }
                });
              }
            });
          });
        }
      }); //

      var hash = {};
      this.dialogToGenInvoice.onlyOne = this.dialogToGenInvoice.onlyOne.reduce(function (item, next) {
        hash[next.variableId] ? "" : hash[next.variableId] = true && item.push(next);
        return item;
      }, []);
      this.dialogToGenInvoice.isShow = true;
    },
    tableSelect: function tableSelect(type) {
      var _this7 = this;

      if (type == "remove") {
        this.filterParams.orderMin = this.orderMin = undefined;
        this.filterParams.orderMax = this.orderMax = undefined;
        this.getItems();
      }

      if (type == "range") {
        if (!(this.orderMin || this.orderMax)) {
          this.$elementMessage("", "error");
          return;
        }

        if (this.orderMin > this.orderMax) {
          this.$elementMessage("", "error");
          return;
        }

        this.filterParams.orderMin = this.orderMin;
        this.filterParams.orderMax = this.orderMax; // this.tableData.forEach(item => {
        //   if(item._LEVEL === 0){
        //     item.children.forEach(store => {
        //       store.children.forEach(order => {
        //         order._VISIBLE = (order.nCode >= this.filterParams.orderMin &&  order.nCode <= (this.filterParams.orderMax ? this.filterParams.orderMax : Number.POSITIVE_INFINITY))
        //       })
        //     })
        //   }
        // });

        this.tableData = this.AllTableData.slice();
        this.tableData.forEach(function (vendor) {
          if (vendor._LEVEL === 0) {
            for (var i1 = 0; vendor.children.length > i1; i1++) {
              var store = vendor.children[i1];

              for (var i2 = 0; store.children.length > i2; i2++) {
                var order = store.children[i2];

                if (!(order.nCode >= _this7.filterParams.orderMin && order.nCode <= (_this7.filterParams.orderMax ? _this7.filterParams.orderMax : Number.POSITIVE_INFINITY))) {
                  store.children.splice(i2, 1);
                  i2--;
                }
              }

              if (!store.children.length) {
                vendor.children.splice(i1, 1);
                i1--;
              }
            }

            if (!vendor.children.length) vendor._VISIBLE = false;
          }
        });
      }

      this.refreshTree();
      this.popCodeFilter = false;
    },
    dateChange: function dateChange() {
      if (this.filterDate.length > 1) {
        this.filterParams.dateFrom = moment(this.filterDate[0]).unix();
        this.filterParams.dateTo = moment(this.filterDate[1]).endOf("d").unix(); //

        this.getItems();
      }
    },
    clearFilter: function clearFilter(type) {
      switch (type) {
        case "vendor":
          this.filterParams.vendorId = "";
          this.filterParams.vendorName = "";
          this.getItems();
          break;

        case "store":
          this.filterParams.shopifyStore = [];
          this.popStoreFilter = false;
          ++this.cascaderKey;
          this.getItems();
          break;

        case "date":
          this.filterParams.timeFilter = "";
          this.filterDate = [];
          this.filterParams.dateFrom = "";
          this.filterParams.dateTo = "";
          this.popDateFilter = false;
          this.getItems();
          break;

        default:
          this.filterParams = JSON.parse(this.filterParamsDefault);
          this.filterDate = [];
          this.tableSelect("remove");
      }
    },
    filterItems: function filterItems(type) {
      var _this8 = this;

      switch (type) {
        case "vendor":
          this.vendors.some(function (vendor) {
            if (vendor.id == _this8.filterParams.vendorId) {
              _this8.filterParams.vendorName = vendor.vendorName;
              return true;
            }
          });
          break;

        case "date":
          this.filterDate = [];
          this.filterParams.dateFrom = "";
          this.filterParams.dateTo = "";
          break;
      }

      this.getItems();
    },
    // openSetPeriod(){
    // },
    toGenInvoice: function toGenInvoice() {
      var paramsArr = [];
      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          vendor.children.forEach(function (store) {
            var skus = [];
            store.children.forEach(function (order) {
              if (order.checked) {
                order.items.forEach(function (sku) {
                  skus.push(sku.id);
                });
              }
            });

            if (skus.length) {
              paramsArr.push({
                relationshipId: [vendor.id],
                skus: skus
              });
            }
          });
        }
      });
      this.submitI = 0;

      if (paramsArr.length) {
        this.dialogToGenInvoice.loading = true;
        this.genInvoice(paramsArr);
      }
    },
    genInvoice: function genInvoice(paramsArr) {
      var _this9 = this;

      paramsArr[this.submitI].paymentType = this.dialogToGenInvoice.paymentType;
      localStorage.setItem("c_defaultPaymentType", JSON.stringify(this.dialogToGenInvoice.paymentType));
      this.$apiCall("api.Invoice.genInvoice", paramsArr[this.submitI], function (r) {
        if (r.ErrorCode == "9998") {
          _this9.dialogToGenInvoice.loading = false;
          _this9.dialogToGenInvoice.isShow = false;
          _this9.dialogNoticeData.isShow = true;
          return;
        }

        if (r.ErrorCode == "9999") {
          _this9.submitI++;

          if (_this9.submitI < paramsArr.length) {
            _this9.genInvoice(paramsArr);
          } else {
            _this9.submitI = 0;
            _this9.dialogToGenInvoice.loading = false;
            _this9.dialogToGenInvoice.isShow = false;

            _this9.$router.push({
              name: "OrderProcessing",
              params: {
                verify: 'email'
              }
            }); // this.getItems(true);
            // this.$elementMessage('Success', "success");

          }
        } else {
          _this9.$elementMessage(r.Message, "error");

          _this9.dialogToGenInvoice.loading = false;
          _this9.submitI = 0;
        }
      });
    },
    changeSkuChecked: function changeSkuChecked(item) {
      var _this10 = this;

      this.tableData.forEach(function (itm) {
        if (itm._LEVEL == 1 && itm._ID == item.parent_ID) {
          _this10.$set(itm, "checked", itm.children.length && _this10.selectedSkuNum(itm) == itm.children.length);
        }
      });
    },
    storeAllChecked: function storeAllChecked(item) {
      var _this11 = this;

      // 
      item.children.forEach(function (sku) {
        _this11.$set(sku, "checked", item.checked);
      });
    },
    selectedSkuNum: function selectedSkuNum(item) {
      // 
      var num = 0;

      if (item._LEVEL == 1) {
        //
        item.children.forEach(function (sku) {
          if (sku.checked) num++;
        });
      } else if (item._LEVEL == 0) {
        //
        item.children.forEach(function (store) {
          store.children.forEach(function (sku) {
            if (sku.checked) num++;
          });
        });
      }

      return num;
    },
    toggleTreeNode: function toggleTreeNode(row) {
      if (row._HAS_CHILDREN) {
        this.setTreeExpand(row, !row._EXPAND);
      }
    },
    setTreeExpand: function setTreeExpand(row, isExpand) {
      var matchObj = XEUtils.findTree(this.tree, function (item) {
        return item === row;
      });
      row._EXPAND = isExpand;

      if (matchObj) {
        XEUtils.eachTree(matchObj.item.children, function (item, index, items, path, parent) {
          item._VISIBLE = parent ? parent._EXPAND && parent._VISIBLE : isExpand;
        });
      }

      this.refreshTree();
    },
    getVendors: function getVendors() {
      var _this12 = this;

      this.$apiCall("api.Relationship.findByCustomer", {}, function (r) {
        if (r.ErrorCode == "9999") {
          _this12.vendors = r.Data.Results;
        } else {
          _this12.$elementMessage(r.Message, "error");
        }
      });
    },
    refreshTree: function refreshTree() {
      var _this13 = this;

      this.tableData.forEach(function (vendor) {
        if (vendor._LEVEL === 0) {
          vendor.children.forEach(function (store, i1) {
            _this13.$set(store, "last", i1 == vendor.children.length - 1); //


            store.children.forEach(function (order, i2) {
              _this13.$set(order, "storeLast", store.last); //


              _this13.$set(order, "last", i2 == store.children.length - 1); //


              _this13.$set(order, "bg", i2 % 2); //

            });
          });
        }
      });
      var treeList = XEUtils.toTreeArray(this.tree);
      this.fullList = treeList;
      this.tableData = treeList.filter(function (item) {
        return item._VISIBLE;
      });
      this.$emit("getTableHeight", this);
    },
    allTreeExpand: function allTreeExpand() {
      XEUtils.eachTree(this.tree, function (item) {
        item._EXPAND = item._HAS_CHILDREN;
        item._VISIBLE = true;
      });
      this.refreshTree();
    },
    getProducts: function getProducts(type) {
      var _this14 = this;

      this.tableData.some(function (item, index) {
        if (item._LEVEL == 1 && item.loading) {
          var params = {
            timeFilter: _this14.filterParams.timeFilter >= 6 ? "" : _this14.filterParams.timeFilter,
            dateFrom: _this14.filterParams.dateFrom,
            dateTo: _this14.filterParams.dateTo,
            relationshipId: item.relationshipId,
            accountIds: [item.id]
          };

          _this14.$apiCall("api.Invoice.shopifyOrders", params, function (r) {
            if (r.ErrorCode == "9999") {
              item.children = [];
              var data = r.Data.Results;

              if (data.length) {
                item._EXPAND = true;
